import React from 'react'
import {Form,Button} from 'semantic-ui-react';
import {useForm} from '../../utils.js/hooks';
import { useMutation, gql } from '@apollo/client';
import {FETCH_POSTS_QUERY} from '../../utils.js/graphql';
export default function PostForm() {
    const {onSubmit,onChange,values} = useForm(createPostCallback,{
        body:''
    });
    const [createPost, { error }] = useMutation(CREATE_POST_MUTATION, {
        variables: values,
        update(proxy, result) {
          const data = proxy.readQuery({
            query: FETCH_POSTS_QUERY,
          });
    
          let newData = [...data.getPosts];
          newData = [result.data.createPost, ...newData];
          proxy.writeQuery({
            query: FETCH_POSTS_QUERY,
            data: {
              ...data,
              getPosts: {
                newData,
              },
            },
          });
          values.body = '';
        },
      });

function createPostCallback(){
createPost();
}
    return (
        <>
       <Form onSubmit={onSubmit}>
           <h2>Create a Post:</h2>
           <Form.Field>
               <Form.Input placeholder="Hi World" name='body' onChange={onChange} value={values.body} erro={error ? true : false}/>
               <Button type='submit' color='teal'>Submit</Button>
           </Form.Field>
       </Form>
       {error && <div className='ui error message' style={{marginBottom:20}}>
           <ul className='list'>
    <li>{error.graphQLErrors[0].message}</li></ul></div>}
       </>
    )
}

const CREATE_POST_MUTATION = gql`
mutation createPost($body:String!){
    createPost(body:$body){
        id body createdAt likes{
            id username createdAt
        }
        likeCount
        comments{
            id body username createdAt
        }
        commentCount
    }
}`